"use client";

import React, { useState } from "react";
import { useRouter } from "next/router";

export default function SignupPage() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
  });
  const router = useRouter();

  const handleSignup = (e) => {
    e.preventDefault();
    // Simulate successful signup
    alert("Signup successful!");
    localStorage.setItem("isLoggedIn", "true"); // Store login state
    router.push("/"); // Redirect to home page
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  return (
    <div className="p-6 bg-black min-h-screen flex items-center justify-center">
      <form
        onSubmit={handleSignup}
        className="bg-gray-800 p-8 rounded-lg shadow-md w-full max-w-md"
      >
        <h1 className="text-3xl font-bold text-red-500 mb-6">Signup</h1>
        <label className="block text-gray-300 mb-2">Name</label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleChange}
          className="w-full px-4 py-2 rounded bg-gray-900 text-white mb-4"
        />
        <label className="block text-gray-300 mb-2">Email</label>
        <input
          type="email"
          name="email"
          value={formData.email}
          onChange={handleChange}
          className="w-full px-4 py-2 rounded bg-gray-900 text-white mb-4"
        />
        <label className="block text-gray-300 mb-2">Password</label>
        <input
          type="password"
          name="password"
          value={formData.password}
          onChange={handleChange}
          className="w-full px-4 py-2 rounded bg-gray-900 text-white mb-6"
        />
        <button
          type="submit"
          className="w-full bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600 transition-colors"
        >
          Signup
        </button>
      </form>
    </div>
  );
}
